<template>
    <div class="container">
        <h1>{{ getTitle }}</h1>
        <button @click="add">кнопка</button>
        <div class="item" v-for="item in getProducts" :key="item.id">
            <h2>Товар: {{  item.name }}</h2>
            <h2>Цена: {{  item.price }}$</h2>
        </div>
    </div>
</template>

<script>
    export default {
        computed: {
            getTitle() {
                return this.$store.state.title
            },
            getProducts() {
                return this.$store.state.products
            }
        },
        methods: {
            add() {
                this.$store.dispatch('addProduct')
            },
            
        },
    }
</script>
